import moment from 'moment';


export class Tag {
    id: string;
    name: string;
    author: string;
    creationDate: moment.Moment;
    modificationDate: moment.Moment;
    tasksCounter: number;


    constructor({ id, name, author, tasksCounter, creationDate, modificationDate}) {
        this.name = name;
        this.id = id || undefined;
        this.author = author;
        this.tasksCounter = tasksCounter || 0;
        this.creationDate = moment(creationDate);
        this.modificationDate = moment(modificationDate);

    }
}
